import React, { useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { actions } from "../../store/store";
import classes from './notes.module.css'
import { v4 as uuidv4 } from 'uuid';
import Description from './../description/description'
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Form from 'react-bootstrap/Form';
import FloatingLabel from 'react-bootstrap/FloatingLabel';
import moment from "moment";

import 'bootstrap/dist/css/bootstrap.min.css';


export default function Notes (props) {
    const [title, setTitle] = useState('')
    const [body, setBody] = useState('')
    const [show, setShow] = useState(false);

    

    let dispatch = useDispatch()
    let notes= useSelector(state => state.notes.notes)
    // if (notes[0].keys(notes[0]).length === 0){
        
    // }

    const handleClose = () => {
        let id = uuidv4()
        dispatch({type:actions.addNote, payload: {id: id, title: title,body:body}})
        console.log(props)
        setShow(false)};

    const handleShow = () => setShow(true);

    const bodyhandler = (e) => {
        setBody(e.target.value)
    }

    const titlehandler = (e) => {
        setTitle(e.target.value)
    }

    const deletehandler = (e) =>{
        debugger
        dispatch({type: actions.deleteNote, payload: e.currentTarget.value})
    }
    

    return(
        <div>
            <Description description={'A simple note app'}/>
            <>
                <Button variant="secondary" onClick={handleShow} style={{'display':'block', 'margin':'0 auto'}}>
                    Add note
                </Button>

                <Modal show={show} onHide={() => setShow(false)}>
                    <Modal.Header closeButton>
                    <Modal.Title>Create note</Modal.Title>
                    </Modal.Header>
                    
                        <Modal.Body>
                            <Form>
                                <Form.Group className="mb-3" controlId="formBasicEmail">
                                    
                                    <FloatingLabel controlId="floatingInputGrid" label="Note title">
                                    <Form.Control type="text" placeholder="" onChange={ titlehandler } maxLength={20}/>
                                    </FloatingLabel>
                                    
                
                                </Form.Group>

                                <FloatingLabel controlId="floatingTextarea2" label="Note body">
                                    <Form.Control
                                    as="textarea"
                                    maxLength={150}
                                    style={{'height':'100px'}}
                                    onChange={ bodyhandler }
                                    />
                                </FloatingLabel>
                                
                            </Form>
                        </Modal.Body>

                    <Modal.Footer>
                        <Button variant="secondary" onClick={() => setShow(false)}>
                            Close
                        </Button>
                        <Button variant="primary" onClick={handleClose}>
                            Add note
                        </Button>
                    </Modal.Footer>
                </Modal>
            </>


            {/* <div className={classes.wrapper}>
                <input id={'title'} placeholder={'Enter title'} onChange={ titlehandler } className={classes.input}/>
                <textarea id={'body'} placeholder={'Enter your note'} onChange={ bodyhandler} className={classes.textarea}/>
                <button className={classes.button} onClick={ addhandler }>Add note</button>
            </div> */}
            
            <div className={classes.list}>
                {notes.map(note => 
                (
                    
                    <div className={classes.note}>
                        <h3 className={classes.title} >{note.title} </h3>
                        <h2 className={classes.body}>{note.body}</h2>
                        <small className={classes.date}>{moment().format('l')}</small>
                        <button className={classes.delete} onClick={ deletehandler } value={note.id}>
                            <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor" class="bi bi-trash3-fill" viewBox="0 0 16 16">
                                <path d="M11 1.5v1h3.5a.5.5 0 0 1 0 1h-.538l-.853 10.66A2 2 0 0 1 11.115 16h-6.23a2 2 0 0 1-1.994-1.84L2.038 3.5H1.5a.5.5 0 0 1 0-1H5v-1A1.5 1.5 0 0 1 6.5 0h3A1.5 1.5 0 0 1 11 1.5Zm-5 0v1h4v-1a.5.5 0 0 0-.5-.5h-3a.5.5 0 0 0-.5.5ZM4.5 5.029l.5 8.5a.5.5 0 1 0 .998-.06l-.5-8.5a.5.5 0 1 0-.998.06Zm6.53-.528a.5.5 0 0 0-.528.47l-.5 8.5a.5.5 0 0 0 .998.058l.5-8.5a.5.5 0 0 0-.47-.528ZM8 4.5a.5.5 0 0 0-.5.5v8.5a.5.5 0 0 0 1 0V5a.5.5 0 0 0-.5-.5Z"/>
                            </svg>
                        </button>

                    </div>
                ))}
            </div>


        </div>

    )
}